/* section universals */
.my-work {
    overflow: overlay;
    width: 100%;
    background-color: #1C3738;
}
.my-work-header {
    width: 140px;
    margin: 160px 0 20px 0;
    font-size: 2em;
    padding: 0 0 5px 5%;
}

.work-section-container {
    width: 100%;
    margin: 40px 0 0 0;
    display: flex;
    align-content: space-between;
    height: fit-content;
}
.work-section-work-container {
    display: flex;
    flex-wrap: wrap;
    height: fit-content;
}
.technologies-div {
    display: flex;
    align-items: center;
}

/*  */
/*  */
/* Trading section */
.tradingview-head {
    font-size: 200%;
    color: #F4FFF8;
    width: 100%;
    height: 35px;
    margin: 5px 0 0 0;
    background-color: #000F08;
    padding: 4px 0 5px 0;
}
.tradingview-head:hover {
    color: #BE996E;
    transition: .3s;
}
.tradingview-img {
    width: 100%;
    max-height: 265px;
    max-width: 600px;
    object-fit: contain;
    opacity: .5;
    transition: .3s;
    margin: 5px 0 0 0;
}
.tradingview-img:hover {
    opacity: 1;
}
.card-description-no-repo {
    padding: 15px 20px 0px 20px;
    color: #F4FFF8;
    line-height: 140%;
    margin-bottom: 0px;
}
.technologies-indicator {
    color: #BE996E;
    padding: 0px 5px 15px 50px;
    text-align:start;
}
.pine-script-img {
    width: 130px;
    opacity: .6;
    border-radius: 5px;
    margin: 0 50px 10px 0;
}